/* src/styles/NavTabs.css */

.nav-tabs .MuiTab-root {
    transition: filter 0.3s ease-in-out;
    letter-spacing: 1px;
    word-spacing: 2px;
    font-weight: bold;
    -webkit-text-stroke-width: 0.1px; /* text outline */
}

.nav-tabs .MuiTab-root:not(.Mui-selected) {
    filter: grayscale(0.5) brightness(0.9) contrast(0.9);
    border-bottom: 2px solid black;
}

.nav-tabs .MuiTab-root:not(.Mui-selected):hover {
    filter: brightness(1.1) contrast(1.1) saturate(1.2);
}

.nav-tabs .MuiTab-root.Mui-selected {
    filter: brightness(1.2) contrast(1.2) saturate(1.3);
    -webkit-text-stroke-width: 0.2px; /* text outline */
}

.nav-tabs .MuiTab-root.Mui-selected:hover {
    filter: brightness(1.3) contrast(1.3) saturate(1.4);
}
